import React from 'react'  
import Button from '@material-ui/core/Button';
import {useSelector,useDispatch} from 'react-redux'

let CartDetails = (props) =>{
    var dispatch = useDispatch()
    var arrs = useSelector(state => state.buy.arr)
    let Del = () =>{
        var priced = props.price
        var Data = priced.split('$')
        var prices = -parseInt(Data[1])
        var Dup = [...arrs]
        Dup[props.index] = undefined
       var NewData =  {type:'Buy',arr:Dup,amount:prices,count:-1}
           dispatch(NewData)
       
       
    }
    return (
        <div id = 'CardDetails' className = {props.name}>
        <h2 id = 'CartQuantity'>{props.quantity}</h2>
        <h2 id = 'CartName'>{props.name}</h2>
        <h2 id = 'CartPrice'  onClick = {Del}>{props.price}<Button id = {props.name + ''+props.price} >X</Button></h2>
        </div>
    )
}
export default CartDetails