import React,{useState} from 'react' 
import {Link } from 'react-router-dom'
import {useSelector} from 'react-redux'
import StoreMallDirectoryOutlinedIcon from '@material-ui/icons/StoreMallDirectoryOutlined';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
let Menu = () =>{
    const No =  useSelector(state => state.buy.count)
    let Disp = () =>{
        document.getElementById('Link1').style.display = 'flex'
    }
    let Close = () =>{
        document.getElementById('Link1').style.display = 'none'
    }
    return(
        <div id = 'Menu'>
        <h1>Store</h1>
        <div id = 'Link'>
        <Link id = 'links' to = '/NewApp'>Home</Link>
        <Link  id = 'NewElement'  to = '/Product'>Products</Link>
        <Link  id = 'links' to = '/Contact'>Contact-Us</Link>
        <Link id = 'links' to = '/Cart'><ShoppingCartOutlinedIcon id = 'cart'/>{No}</Link>
        </div>
        
        <button id = 'menubutton' onClick = {Disp}>Menu</button>
        <div id = 'Link1'>
        <button id = 'CloseButton' onClick = {Close}>X</button>
        <Link id = 'Part1' to = '/NewApp' onClick = {Close}>Home</Link>
        <Link  id = 'Part2'  to = '/Product' onClick = {Close}>Products</Link>
        <Link  id = 'Part3' to = '/Contact' onClick = {Close}>Contact</Link>
        </div>
        
        <Link id = 'link2' to = '/Cart'><ShoppingCartOutlinedIcon id = 'cart'/>{No}</Link>
        </div>
    )
}
export default Menu 